html{
  height: 100%;
}

body{
  height: 100%;
  /*background-image: linear-gradient(to bottom, #94e0e9, #01a7a2);*/
  background-image: linear-gradient(to bottom, #a4e2e9, #399f98);
}

.wrapper{
  display: flex;
  justify-content: center;
  align-items: stretch;
}



.game-container {
  width: calc(95vh);
}

.grid{
  display: grid;
  /*width: calc(70vh);*/
  margin: 10px;
}

.grid > div::before {
  content: "";
  padding-bottom: 100%;
  display: block;
}

.control-panel{
  width: auto;
  border-radius: 5px;
  margin: 10px;
  margin-top: 10vh;
}

.cell{
  width: auto;
  height: auto;
  background-color: white;
  margin: 4%;
  border-radius: 10%;
  display: block;
}

