#flagsBadge, #timerBadge, #gameOverBadge{
  background-color: rgba(255, 255, 255, 0.4);
  border-style: solid;
  border-width: 1px;
  border-color: rgba(255, 255, 255, 0.5);
  padding: 4px 8px;
  line-height: min(30px, max(3vh, 3vw));
}

#gameOverBadge-lost, #timerBadge-lost{
  background-color: rgba(255, 28, 28, 0.4);
  border-style: solid;
  border-width: 1px;
  border-color: rgba(255, 28, 28, 0.5);
  padding: 4px 8px;
  line-height: min(30px, max(3vh, 3vw));
}

#gameOverBadge-won, #timerBadge-won{
  background-color: rgba(31, 177, 3, 0.4);
  border-style: solid;
  border-width: 1px;
  border-color: rgba(31, 177, 3, 0.5);
  padding: 4px 8px;
  line-height: min(30px, max(3vh, 3vw));
}

#timerBadge{
  /*width: 120px;*/
}

.badgeIcon{
  /*margin: 5px;*/
  margin-right: 1vw;
  width: min(min(3vw, 3vh), 20px);
  height: min(min(3vw, 3vh), 20px);
}

#difficultyButton{
  background-color: rgba(255, 255, 255, 0.4);
  border-color: rgba(255, 255, 255, 0.5);
  box-shadow: none;
  font-size: min(20px, min(3vw, 3vh));
  padding: 4px 8px;
  line-height: min(30px, max(3vh, 3vw));
}

#difficultyButton-lost{
  background-color: rgba(255, 28, 28, 0.4);
  border-color: rgba(255, 28, 28, 0.5);
  box-shadow: none;
  font-size: min(20px, min(3vw, 3vh));
  padding: 4px 8px;
  line-height: min(30px, max(3vh, 3vw));
}

#difficultyButton-won{
  background-color: rgba(31, 177, 3, 0.4);
  border-color: rgba(31, 177, 3, 0.5);
  box-shadow: none;
  font-size: min(20px, min(3vw, 3vh));
  padding: 4px 8px;
  line-height: min(30px, max(3vh, 3vw));
}

#difficultyButton:hover{
  background-color: rgba(255, 255, 255, 0.6);
  border-color: rgba(255, 255, 255, 0.8);
}

#difficultyButton-lost:hover{
  background-color: rgba(255, 28, 28, 0.6);
  border-color: rgba(255, 28, 28, 0.8);
  box-shadow: none;
}

#difficultyButton-won:hover{
  background-color: rgba(31, 177, 3, 0.6);
  border-color: rgba(31, 177, 3, 0.8);
  box-shadow: none;
}

#flagCount, #gameEndMessage, #timerDisplay{
  font-size: min(20px, min(3vw, 3vh));
  vertical-align: middle;
  color: #3b3b3b;
}

#resetButton{
  cursor: pointer;
  margin-right: 0;
  margin-left: 1vw;
}


@media only screen and (orientation: portrait){
  .game-container{
    width: calc(100vw);
    margin-top: 15vh;
  }
}

@media only screen and (orientation: landscape){
  .game-container{
    max-width: 800px;
  }
}

@media only screen and (max-width: 768px) {
  #difficultyCol{
    margin-bottom: 10px;
  }
}
